import React, {FC, useRef, useState} from 'react';

import {motion, useMotionValue, useScroll, useTransform, Variants} from 'framer-motion';

import card from '../../assets/images/card.png';
import Phone from '../../assets/images/phone.png';
import swipePhone from '../../assets/images/swipePhone.png';
import cancelIcon from '../../assets/svg/cancelIcon.svg';
import logo from '../../assets/svg/funfun.svg';
import heart from '../../assets/svg/heart.svg';
import heartIcon from '../../assets/svg/heartIcon.svg';
import star from '../../assets/svg/star.svg';
import Stores from '../Stores';

import styles from './style.module.scss';

const NameApp: FC = () => {
	const [width, setWidth] = useState<number>(window.innerWidth);
	const isMobile = width <= 768;
	const extendedRef = useRef(null);
	const containerRef = useRef(null);
	const ref = useRef(null);
	const mobileRef = useRef(null);
	const {scrollY} = useScroll();
	const hidden = useTransform(scrollY, [0, 100], [1, 0]);
	const hiddenMobile = useTransform(scrollY, [0, 350], [1, 0]);
	const opacity = useTransform(scrollY, [0, 200, 300], [0, 0, 1]);
	const mobileOpacity = useTransform(scrollY, [0, 200, 600], [0, 0, 1]);

	const textAnimation = {
		hidden: {opacity: 0},
		visible: (custom: number) => ({
			opacity: 1,
			transition: {delay: custom * 0.2},
		}),
	};

	const {scrollYProgress: scrollHidden} = useScroll({
		target: ref,
	});

	const {scrollYProgress: scrollMobile} = useScroll({
		target: containerRef,
	});

	const {scrollYProgress: scrolly} = useScroll({
		target: extendedRef,
		offset: ['start end', 'end end'],
	});

	const progress = useTransform(scrollY, [0, 400], [-10, 0]);
	const progressMobile = useTransform(scrolly, [0, 0.05], [-10, 0]);

	const x = useTransform(
		scrolly,
		[0.5, 0.68, 0.76, 0.84, 0.92],
		['0rem', '2.6rem', '0rem', '-2.6rem', '0rem'],
	);
	const rotateZ = useTransform(scrolly, [0.5, 0.68, 0.76, 0.84, 0.92], [0, 10, 0, -10, 0]);
	const yCard = useTransform(
		scrolly,
		[0.5, 0.68, 0.76, 0.84, 0.92],
		['0rem', '1.6rem', '0rem', '1.6rem', '0rem'],
	);
	const y = useTransform(scrollHidden, [0, 0.5], ['0rem', '70rem']);
	const opacityCancel = useTransform(scrolly, [0.5, 0.68, 0.76, 0.84, 0.92], [0, 0, 0, 1, 0]);
	const opacityLike = useTransform(scrolly, [0.5, 0.68, 0.76, 0.84, 0.92], [0, 1, 0, 0, 0]);

	return (
		<>
			<motion.section initial="hidden" whileInView="visible" className={styles.nameApp}>
				<img src={heart} className={styles.firstHeart} />
				<img src={heart} className={styles.secondHeart} />
				<img src={heart} className={styles.thirdHeart} />
				<img src={star} className={styles.star} />
				<motion.div ref={containerRef} style={{opacity: isMobile ? hiddenMobile : hidden}}>
					<motion.h1 custom={1} variants={textAnimation} className={styles.logo}>
						FunFun
					</motion.h1>
					<motion.p custom={2} variants={textAnimation} ref={ref} className={styles.text}>
						 ,  
					</motion.p>
					<motion.div custom={3} variants={textAnimation} ref={mobileRef}>
						<Stores style={styles.appGallery} />
					</motion.div>
				</motion.div>
				<motion.p
					style={{opacity: isMobile ? mobileOpacity : opacity}}
					className={styles.today}>
					   ?
				</motion.p>
				<motion.img
					src={Phone}
					style={{rotate: isMobile ? progressMobile : progress}}
					className={styles.phone}
				/>
			</motion.section>
			<motion.section className={styles.swipeContainer} ref={extendedRef} style={{y}}>
				<motion.p
					transition={{
						delay: 0.2,
					}}
					style={{opacity: isMobile ? mobileOpacity : opacity}}
					className={styles.publishWishes}>
					      
				</motion.p>
				<motion.div className={styles.swipeBlock}>
					<motion.div className={styles.phoneContainer}>
						<motion.img src={swipePhone} className={styles.swipePhone} />
						<motion.div style={{rotateZ, x, y: yCard}} className={styles.cardContainer}>
							<motion.img src={card} className={styles.card} />
							<motion.div
								style={{opacity: opacityCancel}}
								className={`${styles.circleIcon} ${styles.cancelBlock}`}>
								<img src={cancelIcon} className={styles.cancelIcon} />
							</motion.div>

							<motion.div
								style={{opacity: opacityLike}}
								className={`${styles.circleIcon} ${styles.likeBlock}`}>
								<img src={heartIcon} className={styles.likeIcon} />
							</motion.div>
						</motion.div>
					</motion.div>
					<motion.div
						initial={{scale: 0.4}}
						whileInView={{scale: 1}}
						transition={{duration: 0.5}}
						className={styles.circle}>
						<motion.p
							initial={{scale: 0.4}}
							whileInView={{scale: 1}}
							transition={{duration: 0.5}}
							viewport={{amount: 0}}
							className={styles.round}>
							    ,     
						</motion.p>
					</motion.div>
				</motion.div>
			</motion.section>
		</>
	);
};

export default NameApp;
