import React, {FC} from 'react';

import {motion} from 'framer-motion';

import Stores from '../Stores';

import styles from './style.module.scss';

const StoresBlock: FC = () => {
	const textAnimation = {
		hidden: {opacity: 0},
		visible: (custom: number) => ({
			opacity: 1,
			transition: {delay: custom * 0.27},
		}),
	};

	return (
		<motion.section className={styles.stores} initial="hidden" whileInView="visible">
			<motion.p custom={1} variants={textAnimation} className={styles.title}>
				   
			</motion.p>
			<motion.div custom={2} variants={textAnimation}>
				<Stores style={styles.storeIcons} />
			</motion.div>
		</motion.section>
	);
};

export default StoresBlock;
