import React, {FC, useRef} from 'react';

import {motion, useScroll, useTransform} from 'framer-motion';

import heart from '../../assets/svg/heart.svg';
import star from '../../assets/svg/star.svg';
import whiteLogo from '../../assets/svg/whiteLogo.svg';

import styles from './style.module.scss';

const Advantages: FC = () => {
	const extendedRef = useRef(null);

	const {scrollYProgress: scrollYProgressIncludingOverlap} = useScroll({
		target: extendedRef,
		offset: ['start end', 'end end'],
	});

	const {scrollYProgress: scrollYProgressLogo} = useScroll({
		target: extendedRef,
		offset: ['start end', 'end end'],
	});

	const xRed = useTransform(
		scrollYProgressIncludingOverlap,
		[0, 0.5, 0.8, 0.95],
		['-80rem', '0rem', '0rem', '20rem'],
	);
	const yRed = useTransform(
		scrollYProgressIncludingOverlap,
		[0, 0.5, 0.8, 0.95],
		['-20rem', '0rem', '0rem', '15rem'],
	);

	const xYellow = useTransform(
		scrollYProgressIncludingOverlap,
		[0, 0.5, 0.8, 0.95],
		['80rem', '0rem', '0rem', '-10rem'],
	);
	const yYellow = useTransform(
		scrollYProgressIncludingOverlap,
		[0, 0.5, 0.8, 0.95],
		['-40rem', '0rem', '0rem', '20rem'],
	);

	const xBlue = useTransform(
		scrollYProgressIncludingOverlap,
		[0, 0.5, 0.8, 0.95],
		['100rem', '0rem', '0rem', '-30rem'],
	);
	const yBlue = useTransform(
		scrollYProgressIncludingOverlap,
		[0, 0.5, 0.8, 0.95],
		['-34rem', '0rem', '0rem', '13rem'],
	);

	const scale = useTransform(
		scrollYProgressIncludingOverlap,
		[0, 0.5, 0.8, 0.95],
		[0.7, 1, 1, 1],
	);

	const opacity = useTransform(scrollYProgressLogo, [0.8, 0.95, 1], [1, 0, 0]);

	return (
		<motion.section className={styles.advantages}>
			<motion.div className={styles.circles} ref={extendedRef}>
				<motion.div className={styles.love} style={{y: yRed, x: xRed, scale, opacity}}>
					
				</motion.div>
				<motion.div
					style={{y: yYellow, x: xYellow, scale, opacity}}
					className={styles.friendship}>
					
				</motion.div>
				<motion.div style={{y: yBlue, x: xBlue, scale, opacity}} className={styles.leisure}>
					
				</motion.div>
				<motion.img src={whiteLogo} alt="Logo" className={styles.whiteLogo} />
				<p className={styles.description}> ,   </p>
			</motion.div>
			<img src={heart} className={styles.fourthdHeart} />
			<img src={star} className={styles.secondStar} />
			<img src={heart} className={styles.fifthHeart} />
		</motion.section>
	);
};

export default Advantages;
