import React, {FC} from 'react';
import Slider from 'react-slick';

import {motion} from 'framer-motion';

import nextArrow from '../../assets/svg/nextArrow.svg';
import prevArrow from '../../assets/svg/prevArrow.svg';

import {CARDS} from './constants';

import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import styles from './style.module.scss';

const SliderBlock: FC = () => {
	var settings = {
		dots: false,
		slidesToShow: 3,
		slidesToScroll: 3,
		infinite: true,
		initialSlide: 1,
		nextArrow: <img src={nextArrow} />,
		prevArrow: <img src={prevArrow} />,
		touchThreshold: 20,
		arrow: false,
		responsive: [
			{
				breakpoint: 768,
				settings: {
					slidesToShow: 1,
					speed: 500,
					slidesToScroll: 1,
					arrows: false,
					centerMode: true,
					centerPadding: '30px',
				},
			},
			{
				breakpoint: 394,
				settings: {
					slidesToShow: 1,
					speed: 500,
					slidesToScroll: 1,
					arrows: false,
					centerMode: true,
					centerPadding: '25px',
				},
			},
		],
	};
	return (
		<motion.section className={styles.container}>
			<motion.p
				initial={{opacity: 0}}
				whileInView={{opacity: 1}}
				transition={{delay: 0.2}}
				className={styles.title}>
				 
			</motion.p>
			<motion.div
				initial={{opacity: 0}}
				whileInView={{opacity: 1}}
				viewport={{amount: 0.4}}
				transition={{delay: 0.4}}
				className={styles.slider}>
				<Slider {...settings}>
					{CARDS.map(item => {
						return (
							<div key={item.title} className={styles.slide}>
								<img src={item.img} className={styles.image} />;
								<div className={styles.slideDescription}>
									<div className={styles.slideTitle}>{item.title}</div>
									<div className={styles.slideText}>{item.text}</div>
								</div>
							</div>
						);
					})}
				</Slider>
			</motion.div>
		</motion.section>
	);
};

export default SliderBlock;
